import { useEffect, useRef, useState } from "react";
import "./App.css";
import ContactPage from "./contact_page/ContactPage";

function App() {
  const [currentInnerWidth, setCurrentInnerWidth] = useState(window.innerWidth);
  const [currentInnerHeight, setCurrentInnerHeight] = useState(
    window.innerHeight
  );

  const [device, setDevice] = useState("desktop");

  const [widthScaleRatio, setWidthScaleRatio] = useState(1.0);
  const [heightScaleRatio, setHeightScaleRatio] = useState(1.0);

  const appContent = useRef(null);

  const maxWidth = 1920;
  const maxWidthTablet = 1250;
  const maxWidthMobile = 768;

  const maxHeight = 963;

  useEffect(() => {
    if (currentInnerWidth <= 1250 && currentInnerWidth > 768) {
      setDevice("desktop");
    } else if (currentInnerWidth <= 768) {
      setDevice("desktop");
    } else {
      setDevice("desktop");
    }
  }, [currentInnerWidth, currentInnerHeight]);

  useEffect(() => {
    window.addEventListener("resize", () => {
      setCurrentInnerWidth(window.innerWidth);
      setCurrentInnerHeight(window.innerHeight);
    });
    window.addEventListener("orientationchange", () => {
      setCurrentInnerWidth(window.innerWidth);
      setCurrentInnerHeight(window.innerHeight);
    });
  }, []);

  useEffect(() => {
    if (device === "mobile") {
      setWidthScaleRatio(currentInnerWidth / maxWidthMobile);
    } else if (device === "tablet") {
      setWidthScaleRatio(currentInnerWidth / maxWidthTablet);
    } else {
      setWidthScaleRatio(currentInnerWidth / maxWidth);
      setHeightScaleRatio(currentInnerHeight / maxHeight);
    }
  }, [currentInnerWidth, currentInnerHeight, device]);

  useEffect(() => {
    appContent.current.style.setProperty("--widthScaleRatio", widthScaleRatio);
    appContent.current.style.setProperty(
      "--heightScaleRatio",
      heightScaleRatio
    );
  }, [widthScaleRatio, heightScaleRatio]);

  useEffect(() => {
    appContent.current.style.setProperty("--maxHeight", maxHeight);
    appContent.current.style.setProperty("--maxWidth", maxWidth);
  }, []);
  return (
    <div className="App" ref={appContent}>
      <ContactPage />
    </div>
  );
}

export default App;
