import { Fragment, memo, useCallback, useState, useEffect } from "react";
import "./ContactPage.css";
import InputField from "../input_field/InputField";
import { Dropbox } from "dropbox";

function ContactPage() {
  console.log(process.env.REACT_APP_DROPBOX_ACCESS_TOKEN);
  const [name, setName] = useState("");
  const [surname, setSurname] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [isSubmitted, setIsSubmitted] = useState(false);

  // Dropbox API setup with access token from environment variables
  const ACCESS_TOKEN = process.env.REACT_APP_DROPBOX_ACCESS_TOKEN; // Use environment variable
  const dbx = new Dropbox({ accessToken: ACCESS_TOKEN });

  // Traffic logging function: Logs the visit if the user has not been logged before
  const logUserVisit = async () => {
    try {
      const currentFile = await dbx.filesDownload({ path: "/traffic.csv" });
      const fileBlob = currentFile.result.fileBlob;
      const fileReader = new FileReader();
      fileReader.readAsText(fileBlob);

      fileReader.onload = async () => {
        const existingCsv = fileReader.result;

        // Generate a unique user ID and check if it's already logged
        let userId = localStorage.getItem("userId");
        if (!userId) {
          userId = `user-${Math.floor(Math.random() * 10000)}`;
          localStorage.setItem("userId", userId);
        }

        const userAlreadyLogged = existingCsv.includes(userId);
        if (userAlreadyLogged) {
          console.log("User already logged.");
          return;
        }

        // Prepare new user data with the visit date in CSV format
        const visitDate = new Date().toISOString();
        const newUserData = `${userId},${visitDate}\n`;

        // Append new user data to the existing CSV
        const updatedCsv = existingCsv + newUserData;

        // Upload the updated CSV file back to Dropbox
        await dbx.filesUpload({
          path: "/traffic.csv",
          contents: updatedCsv,
          mode: { ".tag": "overwrite" } // Overwrite the existing file
        });

        console.log("Traffic CSV file updated with new user visit.");
      };

      fileReader.onerror = () => {
        console.error("Error reading the traffic CSV file.");
      };
    } catch (error) {
      console.error("Error updating traffic CSV file in Dropbox:", error);
    }
  };

  // Log traffic on page load
  useEffect(() => {
    logUserVisit(); // Automatically log the user's visit when the page is loaded
  }, []);

  // Handlers to update state on input change
  const handleNameChange = useCallback((e) => setName(e.target.value), []);
  const handleSurnameChange = useCallback(
    (e) => setSurname(e.target.value),
    []
  );
  const handleEmailChange = useCallback((e) => setEmail(e.target.value), []);
  const handlePhoneChange = useCallback((e) => setPhone(e.target.value), []);

  // Form submission handler
  const handleSubmit = (e) => {
    e.preventDefault();
    if (!name || !surname || !email || !phone) {
      window.alert("Vyplňte prosím všechny pole.");
      return;
    }

    // Prepare new user data in CSV format
    const newUserData = `${name};${surname};${email};${phone}\n`;

    // Upload the new data to Dropbox
    uploadToDropbox(newUserData);

    // Reset the form fields after submission
    setName("");
    setSurname("");
    setEmail("");
    setPhone("");
    setIsSubmitted(true);
  };

  // Function to append new user data to CSV and upload to Dropbox
  const uploadToDropbox = async (newUserData) => {
    try {
      // Step 1: Download the current CSV file from Dropbox
      const currentFile = await dbx.filesDownload({ path: "/data.csv" });
      const fileBlob = currentFile.result.fileBlob;

      // Use FileReader to read the file's content as text
      const fileReader = new FileReader();
      fileReader.readAsText(fileBlob);

      fileReader.onload = async () => {
        const existingCsv = fileReader.result;

        // Step 2: Append new data to the existing CSV
        const updatedCsv = existingCsv + newUserData;

        // Step 3: Upload the updated CSV file back to Dropbox
        await dbx.filesUpload({
          path: "/data.csv",
          contents: updatedCsv,
          mode: { ".tag": "overwrite" } // Overwrite the existing file
        });

        console.log("Form data CSV file updated with new user submission.");
      };

      fileReader.onerror = () => {
        console.error("Error reading the form data CSV file.");
      };
    } catch (error) {
      console.error("Error updating form data CSV file in Dropbox:", error);
    }
  };

  return (
    <Fragment>
      <form className="contact-page-form" onSubmit={handleSubmit}>
        <h1>
          Dobrý den, omlouváme se, ale všechny volné místa jsou zabrané...
        </h1>
        <h2>
          Zanechte nám na sebe kontakt a my se ozveme v případě, že se nám
          uvolní místo.
        </h2>

        <InputField
          placeholder="Jméno"
          type="text"
          value={name}
          onChange={handleNameChange}
        />
        <InputField
          placeholder="Přijmení"
          type="text"
          value={surname}
          onChange={handleSurnameChange}
        />
        <InputField
          placeholder="Email"
          type="email"
          value={email}
          onChange={handleEmailChange}
        />
        <InputField
          placeholder="Telefon"
          type="tel"
          value={phone}
          onChange={handlePhoneChange}
        />

        {isSubmitted ? (
          <h2>Budeme Vás informovat o změnách...</h2>
        ) : (
          <button type="submit">Dejte mi vědět</button>
        )}
      </form>
    </Fragment>
  );
}

export default memo(ContactPage);
